import React from 'react';
import {Switch,BrowserRouter as Router, Redirect, Route} from 'react-router-dom'
import Header from './pages/Header';
import routes from './pages/router.config'
import RouteWithSubRoutes from './pages/RouteWithSubRoutes'

function PrivateRoute({ component: Com, ...rest }) {
	return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com { ...rest }/>
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>
	);
}

const App = () => {
	return <Router>
		<Header />
		<Switch>
			{
				routes.map(item  =>{
					return <RouteWithSubRoutes {...item} />
				})
			}
		
		</Switch>
		</Router>
}

export default App;
export {PrivateRoute};
